body {
  justify-content: center;
  display: flex;
}

.clock {
  border: 5px dotted;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: 10vmin;
  display: flex;
  position: sticky;
}

.clock__point {
  z-index: 1;
  background-color: orange;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.clock__hours {
  z-index: 0;
  background: #00f;
  width: 20px;
  height: 20vmin;
  animation: 60s linear infinite clock;
  position: absolute;
  top: 20vmin;
}

.clock__minutes {
  background: green;
  width: 10px;
  height: 40vmin;
  animation: 5s linear infinite clock;
  position: absolute;
  top: 0;
}

@keyframes clock {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}

